<script>
  export let title = "";
  export let date = "";
  export let journal = "";
  export let url = "";
</script>

<div class="p-4 md:p-8 mb-4 sm:mb-0 rounded-md bg-brand-darker-black shadow-sm">

  <h1 class="mb-5 text-xl flex items-center text-brand-orange">
    <img
      src="./images/typewriter.svg"
      class="h-10 w-10 mr-4"
      alt="cyclones print" />
    {journal}
  </h1>

  <h1 class="mb-5">{title}</h1>

  <div class="flex justify-between items-center">
    <h2 class="mb-1 uppercase text-sm tracking-wider text-brand-strong-blue">
      {date}
    </h2>
    <a
      class="text-sm uppercase tracking-wider text-brand-strong-green flex
      items-center"
      target="_blank"
      href={url}>
      See publication
      <i class="fas fa-arrow-right ml-3" />
    </a>
  </div>

</div>
